@tailwind base;
@tailwind components;
@tailwind utilities;

.dw-progressSlider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 40px;
    height: 40px;
    background: #fff;
    border-radius: 50rem;
    cursor: pointer;
    box-shadow: 0 0 10px;
}

@font-face {
    font-family: 'Articulat';
    src: url('https://s3.ap-southeast-2.amazonaws.com/widget.driveiq/assets/fonts/ArticulatCF-Bold.661c956bc5b18ca2500b.ttf')
        format('truetype');
}

* {
    font-family: Articulat, sans-serif;
}

@layer components {
    .dw-text-base {
        @apply dw-pt-6 dw-text-left;
    }

    .dw-error-validation-horizontal {
        @apply dw-text-red-500 dw-text-base dw-font-semibold dw-pt-2;
    }
    .dw-error-validation-vertical {
        @apply dw-text-red-500 dw-text-base dw-font-semibold;
    }
}

#repaymentWidget {
    @apply dw-p-2;
}
